.container {
    width: 70%;
    margin: 0 auto;
}
.result {
    display: block;
    width: 100%;
    height: 40vh;
    margin-bottom: 1%;
    border: 1px #000 solid;
    color: darkgreen;
}
.messages {
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 15vh;
    padding: 0;
    margin-bottom: 1%;
}
button {
    display: block;
    margin: 0 auto;
    width: 100%;
    height: 10vh;
    padding: 0;
    border-radius: 5px;
    border: none;
    cursor: pointer;
    color: #ffffff;
    background-color: rgb(231, 120, 80);
}